export default function(name) {
  const nameLc = name.toLowerCase()
  return props => nameLc === "oui.sncf" ? <OuiLogo {...props}/> :
    nameLc === "adfab" ? <AdfabLogo {...props} /> :
      nameLc === "ecn" ? <EcnLogo {...props} /> :
      <h3>name</h3>
}